.gamelogo{
    width: 45%;
    transform: translateZ(300px);
}

p{
    padding-top: 50px;
    transform: translateZ(200px);
}
.desc{
    transform-style: preserve-3d;
    .gameInfo{
        pointer-events: none;
        margin-top: 30px;
        transform-style: preserve-3d;
        width: 100%;
        height: 80%;
        & h2{
            float: left;
            margin-top: 20px;
            margin-left: 10px;
            font-weight: lighter;
            color: #F0CC6C;
            transform: translateZ(140px);
        }
        & img{
            width: 80%;
            border-radius: 40px;
            transform: translateZ(100px);
            border: 2px solid #343b3e;
        }
    }
}