.smallHeader,.header{margin-top:30px;color:#f0cb6a;font-weight:lighter;text-shadow:0px 0px 10px #ffe9b0}.smallHeader{padding-left:20px;margin-bottom:10px}.wikiPageList{margin:30px 50px 0 50px;transform:translateZ(50px);list-style:none}.wikiPageList li{margin-top:10px}.wikiPageList li::before{content:"•";color:#f0cb6a;font-weight:bold;display:inline-block;width:1em;margin-left:-1em}.header{text-decoration:underline}.span{color:#f0cb6a}.materialIcon{margin-left:5px;transform:translateY(2px)}.info{margin-bottom:50px}.imageBox{width:55%;min-width:240px;border:1px solid #1c2e39;float:right;margin:20px 20px 20px 20px;position:relative;transform:translateZ(100px);overflow:hidden}.imageBox .image{display:block;width:100%;height:auto}