.list{
    
    min-width: 750px;
    list-style: none;
    width: 100%;
    display: flex;
    justify-content: space-around;
    height: 50px;
    overflow: visible;
    margin: 30px 0;
    width: 100vw;
    background-color: #08090b;
    border: 2px solid #343b3e;
    align-items: center;
    position: relative;

    & a{
        text-decoration: none;
        transition: .1s;
        cursor: pointer;
        width: 220px;
        height: 42px;
        background-color: #181408;
        list-style: none;
        text-align: center;
        line-height: 42px;
        font-size: 23px;
        letter-spacing: 2px;
        font-weight: lighter;
        border-radius: 1px;
        display: inline-block;
        position: relative;

        &.active{
            background-color: #32b8eb;
            // &::before {
            //     pointer-events: none;
            //     content: "";
            //     width: 80%;
            //     height: 70px;
            //     z-index: -1;
            //     position: absolute;
            //     background: linear-gradient(0.5turn, #363636, #46bde9);
            //     left: 50%;
            //     top: -40px;
            //     transform: translateX(-50%);
            // }
        }         
        
        &:hover{
            background-color: #ebf1ef;
            color: black;
        }
    }

    & .pressQ,
    & .pressE{
        z-index: 6;
        position: absolute;
        width: 238px;
        height: 40px;
        background-color: #ebf1ef;
    }
    .smallRect {
        width: 55px;
        height: 35px;
        background-color: #ebf1ef;
        position: absolute;
    }
    .letter{
        color: black;
        position: absolute;
        width: 30px;
        height: 30px;
        border: 1px solid black;
        text-align: center;
        line-height: 26px;
        top: 2px;
    }
    & .pressQ {
        box-shadow: 2px 0px 3px rgb(71 71 71);
        top: 1px;
        left: -200px;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        & .smallRect {
            border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
            right: -20px;
            top: 2px;
            & .letter {
                right: 2px;
                border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
            }
        }
    } 
    & .pressE{
        box-shadow: -2px 0px 3px rgb(71 71 71);
        top: 1px;
        right: -200px;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        & .smallRect {
            left: -20px;
            border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
            top: 2px;
            & .letter {
                border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
                left: 2px;
            }
        }
    }
}

@media (max-width: 1280px) {
    .list{
        &>a{
            width: 180px;
            font-size: 16px;
        }
    }
}
@media (max-width: 1120px) {
    .list{
        &>a{
            width: 150px;
            font-size: 16px;
        }
    }
}
@media (max-width: 1060px) {
    .list{
        &>a:nth-of-type(1){
            text-align: right;
            padding-right: 10px;
        }
        &>a:nth-of-type(6){
            padding-left: 10px;
            text-align: left;
        }
    }
}
@media (max-width: 990px) {
    .list{
        &>a{
            width: 120px;
            font-size: 14px;
        }
    }
}