.content{
    min-width: 750px;
    text-align: center;
    min-height: 120vh;
    width: 95vw;
    margin: 15px auto;
    border: 2px solid #343b3e;
    border-radius: 50px;
    padding: 30px;
    transition: transform .1s linear;
    transform-style: preserve-3d;
    & *{
        pointer-events: none;
    }
}