.advWrap{
    display: flex;
    flex-direction: row;
    transform-style: preserve-3d;
    .sidebar{
        width: 250px;
        position: relative;
        transform: translateZ(50px);
    }
    .desc{
        flex-grow: 1;
        padding-left: 50px;
        padding-bottom: 30px;
        line-height: 30px;
        text-align: left;
        position: relative;
        border: 1px solid #1C2E39;
        border-top-right-radius: 40px;
        border-bottom-right-radius: 40px;
        transform: translateZ(100px);
        color: white;
        transform-style: preserve-3d;
    }
}