.imageBox{
    width: 30%;
    min-width: 240px;
    border: 1px solid #1C2E39;
    float: right;
    margin: 20px 20px 20px 20px;
    position: relative;
    transform: translateZ(100px);
    overflow: hidden;
    .image{
        display: block;
        width: 100%;
        height: auto;
    }
}